// common mixins - start

@mixin eltdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin eltdfImageOverlayHoverStyle($with-hover: true) {
    @if ($with-hover) {
        &:hover {
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include eltdfAbsoluteHolderLayout();

            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;

            @include eltdfTransition(opacity .2s ease-in-out);
        }

    } @else {
        @include eltdfAbsoluteHolderLayout();

        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;

        @include eltdfTransition(opacity .2s ease-in-out);
    }
}

@mixin eltdfStandardPaginationStyle($list_type: null) {
    @include eltdfRelativeHolderLayout(top);

    margin: 15px 0 0;
    clear: both;

    ul {
        @include eltdfRelativeHolderLayout(top);

        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            
            display: inline-block;
            vertical-align: top;
            margin: 0 16px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0;
                color: #211f1f;
                font-size: 11px;
                font-weight: 400;
                letter-spacing: 0.2em;

                &:after{
                    content: "";
                    position: relative;
                    bottom: -4px;
                    display: block;
                    background-color: $default-heading-color;
                    height: 1px;
                    width: 0;
                    @include eltdfTransition(width .3s ease-out);

                }

                &:hover{
                    &:after{
                        width: 28px;
                    }
                }

            }

            &.eltdf-pag-active {
                a {
                    color: $first-main-color;

                    &:after{
                        content: "";
                        position: relative;
                        bottom: -4px;
                        display: block;
                        background-color: $default-heading-color;
                        height: 1px;
                        width: 28px;
                    }
                }
            }

            &.eltdf-pag-prev,
            &.eltdf-pag-next,
            &.eltdf-pag-first,
            &.eltdf-pag-last {
                margin: 0 2px;
                padding: 16px 24px;
                border: 1px solid #f1ebe7;
                box-sizing: border-box;
                a {
                    font-size: 11px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                    &:after{
                        display: none;
                    }
                }
            }
            &.eltdf-pag-prev{
                position: absolute;
                top: -17px;
                left: 0;
            }
            &.eltdf-pag-next{
                position: absolute;
                top: -17px;
                right: 0;
            }

            @if ($list_type == 'shortcode') {
                &.eltdf-pag-prev {
                    a {
                        opacity: 0;
                    }
                }

                &.eltdf-pag-next {
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $first-main-color;

                    &.current {
                        font-size: 11px;
                        color: $first-main-color;
                        &:after {
                            content:'';
                            position: relative;
                            display: block;
                            width: 28px;
                            bottom: -4px;
                            height: 1px;
                            background-color: $first-main-color;
                        }
                    }
                }

                a {
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: "Font Awesome 5 Free";
                            font-size: 11px;
                            line-height: 26px;
                            text-rendering: auto;
                            font-weight: 900;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        margin: 0 2px;
                        padding: 16px 24px;
                        border: 1px solid #f1ebe7;
                        box-sizing: border-box;
                        position: absolute;
                        top: -17px;
                        left: 0;
                      

                        &:before {
                            content: "\f053";
                            
                        }

                        &:after {
                            display: none;
                            transition: none;
                        }
                        &:hover{
                            &:after{
                                width: auto;
                            }
                        }
                    }

                    &.next {
                        margin: 0 2px;
                        padding: 16px 24px;
                        border: 1px solid #f1ebe7;
                        box-sizing: border-box;
                        position: absolute;
                        top: -17px;
                        right: 0;
                        transition: none;

                        &:before {
                            content: "\f054";
                            
                        }
                        &:after {
                            display: none;
                            transition: none;
                        }
                        &:hover{
                            &:after{
                                width: auto;
                            }
                        }
                    }
                }
              
            }
        }
    }
}

@mixin eltdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: inherit;
    font-size: 11px;
    line-height: 2.7em;
    letter-spacing: 0.3em;
    font-weight: 400;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;

    @include eltdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin eltdfButtonSize($size: medium) {
    @if ($size == 'small') {
        padding: 16px 36px;

    } @else if ($size == 'medium') {
        padding: 17px 42px;

    } @else if ($size == 'large') {
        padding: 17px 66px;

    } @else if ($size == 'huge') {
        display: block;
        text-align: center;
        padding: 19px 26px;
    }
}

@mixin eltdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin eltdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $background-light-black-color $important;
    border: 1px solid transparent $important;
}

@mixin eltdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
}

@mixin eltdfButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin eltdfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin eltdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

