// mixins styles - start

%input-style {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $default-border-color;
    border-radius: 0;
    box-sizing: border-box;
    color: $default-text-color;
    cursor: pointer;
    font-family: $default-text-font;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 0.3em;
    font-weight: inherit;
    margin: 0 0 $input-margin;
    outline: 0;
    padding: $input-vertical-top-padding $input-horizontal-padding $input-vertical-bottom-padding;
    position: relative;
    width: 100%;

    -webkit-appearance: none;

    @include eltdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        
    }

    @include eltdfPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

